.loading {
  display: flex;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0px;
  left: 0px;
  align-items: center;
  justify-content: center;
  background: var(--body-bg);
  -webkit-app-region: drag;
}
.loading.inline {
  position: relative;
}
.loading.transparent {
  background: none;
}
.loading > div {
  text-align: center;
  pointer-events: none;
}
.loading h1 {
  margin: 45px 0 12px;
  font-size: 24px;
  color: var(--body);
}
.loading h2 {
  font-size: 16px;
  color: var(--body);
}