*,
*:after,
*:before {
  margin: 0;
  padding: 0;
  border: 0;
  font-weight: 300;
  font-size: 100%;
  box-sizing: border-box;
  user-select: none;
  cursor: default;
  outline: none;
}
*:focus,
*:after:focus,
*:before:focus {
  outline: none;
}

a,
a *,
*:before,
*:after {
  cursor: pointer;
  text-decoration: none;
}

input,
textarea {
  cursor: text;
}

mark {
  background: none;
}

@font-face {
  font-family: "SourceSansPro";
  src: url("~custom-fonts/SourceSansPro-Light.ttf") format("truetype");
  font-weight: 200;
}
@font-face {
  font-family: "SourceSansPro";
  src: url("~custom-fonts/SourceSansPro-Regular.ttf") format("truetype");
  font-weight: 300;
}
@font-face {
  font-family: "SourceSansPro";
  src: url("~custom-fonts/SourceSansPro-SemiBold.ttf") format("truetype");
  font-weight: 400;
}
@font-face {
  font-family: "SourceCodePro";
  src: url("~custom-fonts/SourceCodePro-Medium.ttf") format("truetype");
  font-weight: 300;
}
body,
input,
button {
  font-family: "SourceSansPro";
  -webkit-font-smoothing: antialiased;
}

h1 {
  font-size: 32px;
  font-weight: 200;
  margin-bottom: 26px;
}

p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 16px;
}

strong {
  font-weight: 400;
}

:global .fade-enter {
  opacity: 0;
  transform: scale(0.95);
}
:global .fade-exit {
  opacity: 1;
  transform: scale(1);
}
:global .fade-enter.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms ease-in, transform 200ms ease-out;
}
:global .fade-exit.fade-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms ease-in, transform 200ms ease-out;
  pointer-events: none;
}
:global .slide-enter section {
  opacity: 0;
  transform: translate(50%, 0);
}
:global .slide-enter footer {
  opacity: 0;
}
:global .slide-enter.slide-enter-active section {
  opacity: 1;
  transform: translate(0, 0);
  transition: opacity 0.3s cubic-bezier(0.95, 0.05, 0.795, 0.035), transform 1s cubic-bezier(0.19, 1, 0.22, 1);
}
:global .slide-enter.slide-enter-active footer {
  opacity: 1;
  transition: opacity 0.2s cubic-bezier(0.19, 1, 0.22, 1);
}
:global .slide-exit section {
  opacity: 1;
  transform: translate(0, 0);
  z-index: 10;
}
:global .slide-exit footer {
  opacity: 1;
}
:global .slide-exit.slide-exit-active {
  pointer-events: none;
}
:global .slide-exit.slide-exit-active section {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.3s ease-in, transform 1s cubic-bezier(0.23, 1, 0.32, 1);
}
:global .slide-exit.slide-exit-active footer {
  opacity: 0;
  transition: opacity 0.2s cubic-bezier(0.19, 1, 0.22, 1);
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

body {
  color: var(--body);
  background: var(--body-bg);
}
@media print {
  body {
    background: #fff;
  }
}

main {
  display: flex;
  width: 100vw;
  height: 100vh;
}