.backdrop {
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 1001;
  transition: opacity 0.15s ease-in;
}
@media print {
  .backdrop {
    display: none;
  }
}
.backdrop:before {
  content: "";
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0px;
  left: 0px;
  z-index: 1;
  background: var(--body-bg);
}
.backdrop.confirm, .backdrop.global .backdrop.fullscreen {
  text-align: center;
}
.backdrop.confirm:before, .backdrop.global .backdrop.fullscreen:before {
  opacity: 1;
}
.backdrop.confirm h1, .backdrop.global .backdrop.fullscreen h1 {
  margin-bottom: 20px;
  max-width: none;
}
.backdrop.confirm p, .backdrop.global .backdrop.fullscreen p {
  max-width: none;
}
.backdrop.confirm strong, .backdrop.global .backdrop.fullscreen strong {
  max-width: 40vw;
  word-wrap: break-word;
}
.backdrop.confirm fieldset button, .backdrop.global .backdrop.fullscreen fieldset button {
  margin: 0 20px;
}
.backdrop.global {
  z-index: 1002;
}
.backdrop.inline {
  position: static;
}
.backdrop.fullscreen > div {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  padding-bottom: 80px;
  text-align: center;
}
.backdrop.fullscreen footer {
  position: absolute;
  bottom: 0px;
  height: 70px;
  width: 100%;
  display: flex;
  align-items: stretch;
  bottom: 0px;
  left: 0px;
  margin: 0px;
  padding: 0px;
}
.backdrop.fullscreen footer a, .backdrop.fullscreen footer button {
  margin: 0px;
}
.backdrop.fullscreen footer * {
  flex: 1;
}
.backdrop footer a,
.backdrop footer button {
  margin: 24px;
}
@media screen and (max-width: 860px) {
  .backdrop footer a,
.backdrop footer button {
    margin: 24px 12px;
  }
}
.backdrop .wrapper {
  width: calc(100vw - 40px);
  position: relative;
  z-index: 2;
  transform: scale(1);
  transition: transform 0.2s ease-out;
  text-align: center;
}
.backdrop .wrapper p {
  word-wrap: break-word;
  margin-bottom: 40px;
}
.backdrop .wrapper p > a {
  color: var(--body);
}
.backdrop .wrapper form {
  margin: 30px auto 0;
  padding: 0px;
  border: none;
  max-width: 580px;
  width: calc(100vw - 40px);
}
@media (max-width: 960px) {
  .backdrop .wrapper {
    width: 100vw;
    padding: 0 20px;
  }
}
.backdrop .wrapper section > svg {
  margin-bottom: 20px;
}
.backdrop .wrapper mark {
  color: var(--primary);
}

.hidden {
  pointer-events: none;
  opacity: 0;
}
.hidden .wrapper {
  transform: scale(0.95);
}

.negative {
  color: var(--negative);
}