.tooltip {
  position: relative;
  display: inline-block;
  line-height: 18px;
  vertical-align: middle;
  margin-right: 6px;
}
.tooltip > span {
  display: inline;
  font-family: "Icons";
  cursor: pointer;
}
.tooltip > div {
  position: absolute;
  display: block;
  float: left;
  pointer-events: none;
  background: var(--box-bg);
  padding: 12px;
  border-radius: 4px;
  bottom: calc(100% + 4px);
  left: 100%;
  min-width: 300px;
  line-height: 24px;
  font-size: 14px;
  opacity: 0;
  transition: opacity 0.1s ease-out;
}
.tooltip > div em,
.tooltip > div strong {
  display: block;
  font-style: normal;
  columns: var(--box-body);
  text-align: left;
}
.tooltip > div strong {
  white-space: nowrap;
  color: var(--primary);
  margin-bottom: 8px;
}
.tooltip:hover > div {
  opacity: 1;
}