.wrapper {
  display: inline-block;
  position: relative;
  margin: 0 0 32px;
  padding: 42px 30px 34px;
  text-align: left;
  width: 100%;
  background: var(--box-bg);
  color: var(--box-body);
  padding-top: 68px;
  border-radius: 6px;
}
.wrapper:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 28px;
  background: var(--box-alt);
  border-radius: 6px 6px 0 0;
}
.wrapper > span {
  position: absolute;
  top: 14px;
  left: 20px;
  color: var(--box-body);
  background: var(--box-alt);
  border-radius: 28px;
}
.wrapper p {
  margin-bottom: 0px;
}
.wrapper p + p {
  margin-top: 16px;
}

h1 + .wrapper {
  margin-top: 24px;
}