.addresses p {
  font-family: "SourceCodePro";
  font-size: 12px;
  word-break: break-word;
}
.addresses p mark {
  color: var(--primary);
}
.addresses li {
  display: flex;
}
.addresses li strong {
  flex: 1 0 65px;
  text-align: right;
}
.addresses .spent {
  text-decoration: line-through;
  color: var(--negative);
}
.addresses .spent mark {
  color: var(--negative);
}