.chart {
  width: 100%;
  height: 100%;
  padding: 20px;
  position: relative;
  color: var(--body);
  overflow: hidden;
}
.chart h3 {
  color: var(--primary);
  font-weight: 600;
  font-size: 14px;
}
@media screen and (max-width: 860px) {
  .chart h3 {
    display: none;
  }
}
.chart svg {
  overflow: visible;
}
.chart > div {
  position: absolute;
  top: 70px;
  left: 0px;
  width: calc(100% - 20px);
  height: calc(100% - 240px);
  font-size: 11px;
}
@media screen and (max-width: 860px) {
  .chart > div {
    top: 20px;
    height: calc(100% - 85px);
  }
}
.chart > div > span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--body-alt);
}
.chart footer {
  position: absolute;
  width: 100%;
  bottom: 0px;
  left: 0px;
  padding: 20px;
}
.chart footer nav,
.chart footer ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 860px) {
  .chart footer ul {
    display: none;
  }
}
.chart footer li {
  font-size: 14px;
  list-style: none;
  text-align: left;
  width: 33.33%;
  line-height: 23px;
}
.chart footer li:nth-child(2) {
  text-align: center;
}
.chart footer li:nth-child(3) {
  text-align: right;
}
.chart footer li strong {
  display: block;
  color: var(--body-alt);
  font-weight: 300;
}
.chart footer nav {
  margin-bottom: 16px;
  padding-top: 24px;
  position: relative;
}
@media screen and (max-width: 860px) {
  .chart footer nav {
    margin-bottom: 0px;
  }
  .chart footer nav:before {
    display: none;
  }
}
.chart footer nav:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 1px;
  background: var(--body-bg);
}
.chart footer nav p {
  line-height: 34px;
  font-size: 24px;
  text-align: center;
  margin: -4px 0;
}
.chart footer nav button {
  display: block;
  height: 28px;
  line-height: 26px;
  font-size: 11px;
  min-width: 60px;
  background: none;
  border-radius: 24px;
}
.chart .label {
  background: var(--body-bg);
  color: var(--secondary);
  padding: 4px 12px;
  border-radius: 4px;
  font-size: 12px;
}