.checkbox {
  display: block;
  text-align: left;
  padding-left: 40px;
  margin-bottom: 20px;
  position: relative;
  line-height: 24px;
  cursor: pointer;
}
.checkbox:before {
  display: block;
  content: "";
  width: 24px;
  height: 24px;
  border-radius: 3px;
  position: absolute;
  top: 0px;
  left: 0px;
  border: 1px solid var(--body);
}
.checkbox span {
  pointer-events: none;
}
.checkbox.on:after {
  position: absolute;
  top: 2px;
  left: 8px;
  content: "";
  display: block;
  width: 8px;
  height: 14px;
  border: solid var(--body);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
.checkbox.small {
  line-height: 18px;
  font-size: 14px;
}
.checkbox.small:before {
  width: 18px;
  height: 18px;
}
.checkbox.small:after {
  left: 6px;
  width: 6px;
  height: 11px;
}
.checkbox.disabled {
  opacity: 0.3;
  pointer-events: none;
}

.checkbox + .checkbox {
  margin-top: -10px;
}