.print {
  display: none;
}
@media print {
  .print {
    display: block;
  }
  .print img,
.print svg,
.print div {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
  }
  .print svg text {
    font-family: "SourceCodePro";
    font-size: 20px;
    fill: #231f20;
  }
  .print canvas {
    position: absolute;
    left: 50%;
    top: 81.5%;
    width: 9vh !important;
    height: 9vh !important;
    transform: translate(-175%, 0);
  }
}