.wave {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100vw;
}
.wave > div {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-size: auto 100%;
  background-position: left bottom;
  background-repeat: repeat-x;
  transition: background-position 3s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.wave > div:last-child {
  background-position: 50% bottom;
}
@media print {
  .wave {
    display: none;
  }
}