.toggle {
  display: block;
  margin: 32px 0;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  width: 100%;
  text-align: center;
}
.toggle span {
  text-transform: capitalize;
  width: 100px;
  cursor: pointer;
  color: var(--body);
}
.toggle span:first-child {
  padding-right: 70px;
  position: relative;
  color: var(--primary);
}
.toggle span:first-child:after {
  content: "";
  width: 50px;
  height: 20px;
  position: absolute;
  top: 0px;
  right: 10px;
  border-radius: 15px;
  border: 1px solid var(--body);
}
.toggle span:first-child:before {
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  top: 2px;
  right: 42px;
  border-radius: 9px;
  border: 1px solid var(--primary);
  background: var(--primary);
  transition: all 0.2s cubic-bezier(0, 0, 0.2, 1);
}
.toggle.on span {
  color: var(--primary);
}
.toggle.on span:first-child {
  color: var(--body);
}
.toggle.on span:first-child:before {
  right: 12px;
}