.dropzone {
  display: flex;
  position: fixed;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
  background: var(--body-bg);
  pointer-events: none;
  z-index: 10;
  opacity: 0;
}
.dropzone.active {
  opacity: 1;
}
.dropzone svg {
  display: block;
  margin-bottom: 20px;
}