.nav {
  display: flex;
  padding: 20px 0;
  margin: 0px 20px;
  height: 80px;
  border-bottom: 1px solid var(--body-bg);
}
@media (max-width: 320px) {
  .nav {
    display: none;
  }
}
.nav > a {
  color: var(--body);
  margin: 0 22px 0 0;
  font-weight: 400;
}
.nav > a.empty {
  color: var(--body-alt);
}
.nav > a.disabled {
  color: var(--body-alt);
  pointer-events: none;
}
.nav > a small {
  font-size: 13px;
  margin: 0 2px;
}
.nav > ul {
  margin: 0px;
  padding: 0px;
  position: relative;
  z-index: 2;
}
.nav > ul > li {
  list-style: none;
  position: absolute;
  width: auto;
  white-space: nowrap;
  top: 48px;
}
.nav > ul > a,
.nav > ul > li > a,
.nav > ul > li > div {
  display: none;
  flex: 0;
  line-height: 40px;
  font-size: 14px;
  padding: 0 10px;
  vertical-align: middle;
  background: var(--body-bg);
  width: 100%;
}
.nav > ul > a.active,
.nav > ul > li > a.active,
.nav > ul > li > div.active {
  color: var(--primary);
}
.nav > ul > a.disabled,
.nav > ul > li > a.disabled,
.nav > ul > li > div.disabled {
  color: var(--body-alt);
  pointer-events: none;
}
.nav > ul > a span,
.nav > ul > li > a span,
.nav > ul > li > div span {
  margin-left: 4px;
}
.nav > ul > a {
  display: block;
  float: left;
  width: auto;
  background: var(--body-bg);
  border-radius: 4px;
  line-height: 40px;
  font-weight: 400;
}
.nav > ul > li div {
  color: var(--body);
}
.nav > ul > li div .checkbox {
  display: block;
  position: relative;
  padding-right: 40px;
}
.nav > ul > li div .checkbox:after {
  content: "";
  width: 28px;
  height: 15px;
  position: absolute;
  top: 14px;
  right: 1px;
  border-radius: 15px;
  border: 1px solid var(--body);
}
.nav > ul > li div .checkbox:before {
  content: "";
  width: 9px;
  height: 9px;
  position: absolute;
  top: 17px;
  right: 16px;
  border-radius: 9px;
  border: 1px solid var(--body);
  transition: all 0.2s cubic-bezier(0, 0, 0.2, 1);
}
.nav > ul > li div .checkbox.on:before {
  border-color: var(--primary);
  background: var(--primary);
  right: 4px;
}
.nav > ul:hover > a,
.nav > ul:hover > li > div,
.nav > ul:hover > li > a {
  display: block;
  clear: both;
  position: relative;
}
.nav > ul:hover > a:after,
.nav > ul:hover > li > div:after,
.nav > ul:hover > li > a:after {
  display: block;
  content: "";
  width: calc(100% + 4px);
  left: 0px;
  bottom: 0px;
  border-bottom: 1px solid var(--body-alt);
}
.nav > ul:hover > a:first-child,
.nav > ul:hover > li > div:first-child,
.nav > ul:hover > li > a:first-child {
  border-radius: 0 4px 0 0;
}
.nav > ul:hover > a:last-child,
.nav > ul:hover > li > div:last-child,
.nav > ul:hover > li > a:last-child {
  border-radius: 0 0 4px 4px;
}
.nav > ul:hover > a:last-child:after,
.nav > ul:hover > li > div:last-child:after,
.nav > ul:hover > li > a:last-child:after {
  display: none;
}
.nav > ul:hover > a:hover,
.nav > ul:hover > li > div:hover,
.nav > ul:hover > li > a:hover {
  color: var(--primary);
}
.nav > ul:hover > a {
  border-radius: 4px 4px 0 0;
}
.nav > ul:hover > a:after {
  display: none;
}
.nav > ul:hover > a:before {
  content: "";
  width: 100%;
  height: 20px;
  background: var(--body-bg);
  position: absolute;
  top: 38px;
  left: 0px;
}

.list {
  position: absolute;
  top: 100px;
  left: 0px;
  width: 100%;
  height: calc(100% - 100px);
  padding: 0 0 20px 20px;
}
@media (max-width: 320px) {
  .list {
    padding: 20px 0;
    top: 160px;
    height: calc(100vh - 210px);
    width: calc(100% + 20px);
    background: var(--box-bg);
  }
}
.list a {
  list-style: none;
  font-size: 14px;
}
.list a > div {
  list-style: none;
  display: flex;
  line-height: 32px;
  cursor: pointer;
  width: 100%;
  padding: 0 10px;
}
.list a > div > span {
  width: 50%;
  padding: 0 2%;
}
.list a > div > span:nth-child(1) {
  width: 30px;
  margin-top: 10px;
  padding-left: 0px;
}
@media (max-width: 320px) {
  .list a > div > span:nth-child(1) {
    padding-left: 10px;
  }
}
.list a > div > span:nth-child(2) {
  text-align: left;
  width: 50%;
}
.list a > div > strong:nth-child(3) {
  width: 20%;
  text-align: left;
}
.list a > div > span:nth-child(4) {
  width: 25%;
  text-align: right;
}
.list a:hover > div {
  background: var(--body-bg);
}
.list a.received span:nth-child(1),
.list a.received span:nth-child(4) {
  color: var(--primary);
}
.list a.sent span:nth-child(1),
.list a.sent span:nth-child(4) {
  color: var(--secondary);
}
.list a.pending div {
  opacity: 0.5;
}
.list .empty {
  display: block;
  position: absolute;
  top: 50%;
  left: 0px;
  text-align: center;
  width: 100%;
  opacity: 0.5;
  padding: 0;
  margin: -20px 0 0;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.refresh {
  display: block;
  position: absolute;
  top: 28px;
  right: 0px;
  margin: 0px;
  height: 24px;
  width: 24px;
  text-align: center;
}
.refresh:hover {
  color: var(--primary);
}
.refresh.busy {
  pointer-events: none;
  color: var(--body-alt);
}
.refresh.loading {
  pointer-events: none;
  animation: spin 0.8s ease infinite;
}

.search {
  margin: 0 50px 0 20px;
  flex: 1;
  position: relative;
}
.search input {
  line-height: 40px;
  height: 40px;
  width: 40px;
  background: var(--body-bg);
  border: none;
  color: var(--input-color);
  padding: 0 30px 0 10px;
  border-radius: 4px;
  cursor: pointer;
  transition: width 0.2s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.search input:focus, .search input.filled {
  width: 100%;
  padding: 0 40px 0 10px;
}
.search input::placeholder {
  font-size: 14px;
  color: var(--body-alt);
}
.search span {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  right: calc(100% - 30px);
  background: none;
  color: var(--body);
  pointer-events: none;
  transition: right 0.2s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.search input:focus + div > span,
.search input.filled + div > span {
  right: 10px;
}
.search input.filled + div > span {
  pointer-events: all;
  cursor: pointer;
}
.search input.filled + div > span:hover {
  opacity: 0.8;
}
.search input.filled + div .tooltip {
  opacity: 1;
}
.search .tooltip {
  position: absolute;
  pointer-events: none;
  z-index: 2;
  font-size: 14px;
  background: var(--bar-bg);
  color: var(--bar);
  width: 342px;
  border-radius: 4px;
  line-height: 32px;
  padding: 12px 14px 12px 16px;
  top: 48px;
  left: 0px;
  opacity: 0;
}
.search .tooltip li {
  list-style: none;
}
.search .tooltip strong {
  display: inline-block;
  color: var(--primary);
  width: 50px;
}
.search .tooltip:after {
  content: "";
  position: absolute;
  top: -6px;
  left: 16px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 6px 6px 6px;
  border-color: transparent transparent var(--bar-bg) transparent;
}

.popup {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: var(--box-bg);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.15s ease-out;
  cursor: pointer;
  z-index: 3;
}
.popup > div {
  position: absolute;
  top: 50%;
  left: 20px;
  width: calc(100% - 40px);
  min-height: 100px;
  transform: translate(0, -50%) scale(0.9);
  background: var(--body-bg);
  padding: 20px;
  border-radius: 4px;
  transition: transform 0.2s cubic-bezier(0, 0, 0.2, 1);
}
.popup > div p:first-child {
  display: flex;
  justify-content: space-between;
}
.popup > div p strong {
  padding-top: 21px;
}
.popup > div p small {
  font-size: 12px;
  text-align: right;
}
.popup > div p em {
  display: block;
  font-style: normal;
  color: var(--body);
}
.popup > div p.hash {
  display: block;
  font-family: "SourceCodePro";
  word-break: break-word;
}
.popup > div p.hash > span {
  display: block;
}
.popup > div .message {
  display: block;
  margin: 26px 0 20px;
  word-break: break-word;
  height: 70px;
  position: relative;
}
.popup > div .message strong {
  display: block;
  margin-bottom: 4px;
  font-size: 12px;
  padding-top: 0px;
}
.popup > div h6 {
  font-size: 12px;
  margin-bottom: 0px;
}
.popup > div .received p:first-child strong {
  color: var(--primary);
}
.popup > div .sent p:first-child strong {
  color: var(--secondary);
}
.popup > div .pending p:first-child small {
  color: var(--negative);
}
.popup > div .confirmed p:first-child small {
  color: var(--positive);
}
.popup.on {
  opacity: 1;
  pointer-events: all;
}
.popup.on > div {
  transform: translate(0, -50%) scale(1);
}
.popup .addresses strong {
  display: block;
  margin-bottom: 8px;
  font-size: 12px;
}
.popup .addresses p {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  line-height: 14px;
  font-family: "SourceCodePro";
  word-break: break-word;
}
.popup .addresses p em {
  flex: 0 0 20%;
  text-align: right;
  font-family: "SourceSansPro";
  font-weight: 400;
}
.popup .addresses p mark {
  color: var(--primary);
}
.popup .addresses > div {
  max-height: calc(100vh - 640px);
  width: calc(100% + 20px);
  position: relative;
  overflow: hidden;
}
@media (max-width: 940px) {
  .popup .addresses > div {
    max-height: calc(100vh - 680px);
  }
}
.popup nav {
  text-align: center;
}
.popup nav button:first-of-type {
  margin-bottom: 10px;
  min-width: 120px;
}