.balance {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
@media (max-width: 320px) {
  .balance {
    height: 160px;
    padding-top: 15px;
    position: relative;
  }
}
.balance h3 {
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.5px;
}
.balance h1 {
  font-size: 64px;
  line-height: 64px;
  margin: 12px 0px;
  cursor: pointer;
}
@media screen and (max-width: 860px) {
  .balance h1 {
    margin: 4px 0;
  }
}
.balance h1 small {
  font-size: 21px;
  line-height: 21px;
  margin-left: 6px;
}
.balance h2 {
  margin-bottom: 15px;
}
.balance > a {
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -5px;
}
.balance > a:last-of-type {
  left: auto;
  right: 20px;
}