@keyframes slideRight {
  from {
    transform: translate(100%, 0);
  }
  to {
    transform: translate(0, 0);
  }
}
@keyframes slideLeft {
  from {
    transform: translate(-100%, 0);
  }
  to {
    transform: translate(0, 0);
  }
}
.dashboard {
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  flex-direction: column;
  padding: 20px;
  overflow: hidden;
}
.dashboard.windows {
  padding-top: 50px;
}
@media (max-width: 320px) {
  .dashboard {
    padding: 0px;
  }
}
.dashboard > div {
  width: 100%;
}
.dashboard > div:nth-child(1) {
  flex: 0 0 185px;
  margin-bottom: 20px;
  z-index: 3;
}
@media screen and (max-width: 860px) {
  .dashboard > div:nth-child(1) {
    height: 230px;
    transition: height 0.2s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  .dashboard > div:nth-child(1).balanceOpen {
    height: 540px;
  }
}
@media (max-width: 320px) {
  .dashboard > div:nth-child(1) {
    height: 220px;
    margin-bottom: 0px;
  }
}
.dashboard > div:nth-child(2) {
  flex: 1;
  display: flex;
  z-index: 2;
}
@media screen and (max-width: 860px) {
  .dashboard > div:nth-child(2) {
    display: block;
    max-height: calc(100vh - 290px);
  }
}
@media (max-width: 320px) {
  .dashboard > div:nth-child(2) {
    max-height: calc(100vh - 220px);
  }
}
.dashboard > div:nth-child(2) section {
  width: calc(50% - 10px);
  height: 100%;
  overflow: hidden;
  background: var(--box-bg);
}
@media screen and (max-width: 860px) {
  .dashboard > div:nth-child(2) section {
    width: 100%;
    height: calc(50% - 10px);
    margin-bottom: 20px;
    transition: height 0.2s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
}
.dashboard > div:nth-child(2) section:first-child {
  margin-right: 20px;
}
@media (max-width: 320px) {
  .dashboard > div:nth-child(2) section {
    height: 100%;
  }
  .dashboard > div:nth-child(2) section:first-child {
    display: none;
  }
}
@media screen and (max-width: 860px) {
  .dashboard > div:nth-child(2).history section:first-child {
    height: 100%;
  }
  .dashboard > div:nth-child(2).history section:last-child {
    opacity: 0;
  }
}
.dashboard section {
  position: relative;
  border-radius: 5px;
}

.balance {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 3;
  background: none;
}
@media screen and (max-width: 860px) {
  .balance {
    background: var(--box-bg);
    overflow: hidden;
  }
}
@media screen and (max-width: 860px) {
  .balance > div:first-child {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 160px;
    opacity: 1;
    padding-top: 20px;
    position: relative;
    z-index: 2;
    background: var(--box-bg);
  }
}
.balance > div:nth-child(2),
.balance > div:nth-child(3) {
  position: absolute;
  background: var(--box-bg);
  border-radius: 5px;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}
.balance > div:nth-child(2) {
  transition: opacity 420ms ease-out, transform 420ms ease-out;
  border-radius: 4px;
}
.balance > div:nth-child(2).openMid {
  opacity: 0.2;
  transform: scale(0.92);
}
.balance > div:nth-child(3) {
  pointer-events: none;
  z-index: 3;
}
.balance > div:nth-child(3).openRight, .balance > div:nth-child(3).openLeft {
  transition: none;
  animation: slideRight 420ms cubic-bezier(0.4, 0, 0.2, 1);
  pointer-events: all;
}
@media screen and (max-width: 860px) {
  .balance > div:nth-child(3).openRight, .balance > div:nth-child(3).openLeft {
    animation: slideRight 220ms ease-in;
  }
}
.balance > div:nth-child(3).openLeft {
  animation: slideLeft 420ms cubic-bezier(0.4, 0, 0.2, 1);
}
@media screen and (max-width: 860px) {
  .balance > div:nth-child(3).openLeft {
    animation: slideLeft 220ms ease-in;
  }
}
.balance > div:nth-child(3).close {
  transition: all 200ms ease-out;
  transform: scale(0.9);
  opacity: 0;
  pointer-events: none;
}
.balance > div:nth-child(2) {
  display: flex;
}
@media screen and (max-width: 860px) {
  .balance > div:nth-child(2) {
    padding-bottom: 64px;
  }
  .balance > div:nth-child(2) > div {
    display: none;
  }
}
.balance > div:nth-child(2) a {
  flex: 0 0 185px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: var(--box-alt);
}
.balance > div:nth-child(2) a:first-of-type {
  border-radius: 4px 0 0 4px;
}
.balance > div:nth-child(2) a:last-of-type {
  border-radius: 0 4px 4px 0;
}
@media screen and (min-width: 861px) {
  .balance > div:nth-child(2) a > div {
    display: block;
    position: relative;
    margin: 32px auto 20px;
    width: 64px;
    height: 64px;
    padding: 18px;
    text-align: center;
    line-height: 38px;
  }
  .balance > div:nth-child(2) a > div:before, .balance > div:nth-child(2) a > div:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% + 12px);
    height: calc(100% + 12px);
    border: 1px solid var(--body-alt);
    border-radius: 100%;
    transition: all 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
    z-index: 2;
  }
  .balance > div:nth-child(2) a > div:after {
    width: 100%;
    height: 100%;
    background: var(--box-bg);
    border: none;
    transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);
    z-index: 1;
  }
  .balance > div:nth-child(2) a > div span {
    position: relative;
    z-index: 3;
  }
}
.balance > div:nth-child(2) a:hover > div:before {
  transform: translate(-50%, -50%) scale(1.15);
  border-color: var(--body);
}
.balance > div:nth-child(2) a:hover > div:after {
  transform: translate(-50%, -50%) scale(0.9);
}
@media screen and (max-width: 860px) {
  .balance > div:nth-child(2) a {
    position: absolute;
    width: calc(50% - 1px);
    height: 64px;
    bottom: 0px;
    border-radius: 0 0 0 6px;
    flex-direction: row;
    font-size: 14px;
  }
  .balance > div:nth-child(2) a:first-of-type {
    border-radius: 0 0 0 4px;
  }
  .balance > div:nth-child(2) a:last-of-type {
    border-radius: 0 0 4px 0;
    left: calc(50% + 1px);
  }
  .balance > div:nth-child(2) a p {
    margin-bottom: 4px;
  }
  .balance > div:nth-child(2) a span {
    margin: 0 20px 0 0;
    font-size: 26px !important;
  }
}
@media (max-width: 320px) {
  .balance > div:nth-child(2) a {
    height: 48px;
  }
  .balance > div:nth-child(2) a div span {
    font-size: 18px !important;
  }
  .balance > div:nth-child(2) a p {
    font-size: 14px;
  }
}
.balance > div:nth-child(2) > div {
  flex: 1;
  text-align: center;
}
@media screen and (max-width: 860px) {
  .balance > div:nth-child(3) {
    height: calc(100% - 160px);
    top: 160px;
  }
}