@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.receive {
  display: flex;
  padding: 20px;
  border-radius: 5px;
}
@media screen and (max-width: 860px) {
  .receive {
    display: block;
    position: relative;
    padding: 40px 20px 0;
  }
  .receive:before {
    content: "";
    position: absolute;
    width: calc(100% - 40px);
    height: 2px;
    top: 0px;
    left: 20px;
    background: var(--body-alt);
  }
}
.receive > div {
  flex: 1;
}
@media screen and (max-width: 860px) {
  .receive > div {
    width: 100%;
  }
}
.receive > div input {
  border: none;
  line-height: 40px;
  padding-left: 10px;
  font-size: 14px;
}
.receive > div:nth-child(1) {
  display: flex;
  flex: 0 1 360px;
  position: relative;
}
.receive > div:nth-child(1):after {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 2px;
  height: calc(100%);
  background: var(--body);
  opacity: 0.1;
}
@media screen and (max-width: 860px) {
  .receive > div:nth-child(1) {
    height: 170px;
  }
  .receive > div:nth-child(1):after {
    display: none;
  }
}
.receive > div:nth-child(1) button {
  flex: 1;
}
.receive > div:nth-child(1) p {
  display: block;
  word-wrap: break-word;
  word-break: break-word;
  letter-spacing: 1px;
  font-family: SourceCodePro;
  font-size: 14px;
  line-height: 25px;
  padding-left: 20px;
  max-width: 170px;
  margin-bottom: 0px;
}
.receive > div:nth-child(1) p span {
  color: var(--primary);
}
.receive > div:nth-child(2) {
  max-width: 520px;
  margin-left: 40px;
}
.receive svg {
  display: block;
  background: #fff;
  border-radius: 5px;
  border: 5px solid white;
}
.receive footer {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}
.receive footer > a,
.receive footer > span {
  width: calc(50% - 5%);
}
.receive footer > a:first-child,
.receive footer > span:first-child {
  margin-right: 10%;
}
.receive footer span button {
  width: 100%;
}

.loading button span {
  animation: spin 0.8s ease infinite;
}

.empty > div:nth-child(1) svg {
  display: none;
}
.empty > div:nth-child(2) div,
.empty > div:nth-child(2) footer > span {
  opacity: 0.15;
  pointer-events: none;
}