.scrollbar {
  position: relative;
  height: 100%;
  overflow: hidden;
  padding-right: 20px;
}
.scrollbar :global .ps__rail-y {
  display: block;
  position: absolute;
  top: 0px;
  left: auto !important;
  right: 7px;
  opacity: 0;
  width: 6px;
  border-radius: 6px;
  transition: opacity 200ms cubic-bezier(0.55, 0, 0.1, 1);
}
.scrollbar :global .ps__rail-y .ps__thumb-y {
  cursor: pointer;
  border-radius: 6px;
  background: var(--body-alt);
  position: absolute;
  left: 0px;
  width: 6px;
}
.scrollbar:hover :global .ps__rail-y {
  opacity: 1;
}