.tray > nav:last-child {
  position: absolute;
  bottom: 0px;
  width: 100%;
  display: flex;
  align-items: stretch;
}
.tray > nav:last-child a {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 52px;
}
.tray > nav:last-child a:first-child {
  border-right: solid 2px var(--box-bg);
}
.tray > nav:last-child a span {
  margin-right: 8px;
}
.tray > nav:last-child a:hover {
  background: var(--box-bg);
}
.tray .intro {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.tray .intro h2 {
  margin: 20px 0 0;
}
.tray .intro button {
  position: absolute;
  bottom: 0px;
  width: 100%;
}