.titlebar {
  width: 100vw;
  height: 40px;
  position: fixed;
  top: 0px;
  left: 0px;
  -webkit-app-region: drag;
  z-index: 99998;
}
.titlebar:after {
  display: block;
  content: "";
  width: 100vw;
  height: 6px;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-app-region: no-drag;
  z-index: 2;
}

.windows {
  display: flex;
  width: 100%;
  height: 40px;
  align-items: center;
  justify-content: flex-end;
  padding: 0 20px;
}
@media (max-width: 940px) {
  .windows {
    padding-left: 26px;
  }
}
.windows > a {
  margin-left: 40px;
  width: 21px;
  height: 21px;
  -webkit-app-region: no-drag;
}
.windows > a * {
  -webkit-app-region: no-drag;
}
.windows > a:first-child {
  margin: 0 auto 0 0;
}
.windows > a:first-child.dark svg path {
  fill: var(--bar);
}
.windows > a:first-child.light svg path {
  fill: var(--body);
}
.windows > a svg {
  margin: 3px;
}
.windows > a svg path {
  fill: var(--body);
}
.windows > a:hover svg path {
  fill: var(--primary);
}