.input {
  display: block;
  width: 100%;
  max-width: 520px;
  margin: 0 auto;
  padding: 25px 0 30px;
  transition: opacity 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
  position: relative;
}
.input fieldset {
  border: none;
  position: relative;
}
.input fieldset input,
.input fieldset textarea,
.input fieldset select,
.input fieldset .editable,
.input fieldset .selectable {
  font-family: "SourceSansPro";
  font-weight: 300;
  font-size: var(--font-size-default);
  line-height: 46px;
  width: 100%;
  border: 1px solid var(--input-border);
  background: var(--input-bg);
  color: var(--input);
  padding: 0 20px 0 14px;
  resize: none;
  -webkit-appearance: none;
  border-radius: 5px;
  transition: border 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.input fieldset input:focus,
.input fieldset textarea:focus,
.input fieldset select:focus,
.input fieldset .editable:focus,
.input fieldset .selectable:focus {
  outline: none;
  border-color: var(--input-focus);
}
.input fieldset input:disabled,
.input fieldset textarea:disabled,
.input fieldset select:disabled,
.input fieldset .editable:disabled,
.input fieldset .selectable:disabled {
  pointer-events: none;
  opacity: 0.5;
}
.input fieldset input[type=number]::-webkit-inner-spin-button, .input fieldset input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input fieldset:hover .tooltip {
  opacity: 1;
  transition: 0.2s ease-in;
}
.input fieldset .editable {
  display: block;
  font-family: "SourceCodePro";
  white-space: nowrap;
  overflow: hidden;
  height: 46px;
  text-align: left;
  max-width: 520px;
  padding: 0 40px 0 0;
}
.input fieldset .editable > div {
  padding: 0 20px;
  overflow: hidden;
}
.input fieldset .editable > div span {
  display: inline-block;
  position: relative;
  height: 100%;
}
.input fieldset .editable > div span:before {
  display: inline-block;
  position: absolute;
  left: 0px;
  content: attr(data-letter);
  pointer-events: none;
  line-height: 44px;
}
.input fieldset .selectable {
  position: relative;
  text-align: left;
  height: 46px;
  cursor: pointer;
}
.input fieldset .selectable:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 4px 0 4px;
  border-color: var(--input-alt) transparent transparent transparent;
  position: absolute;
  right: 12px;
  top: calc(50% - 2px);
  pointer-events: none;
}
.input fieldset small {
  position: absolute;
  white-space: nowrap;
  left: 1px;
  top: -24px;
  color: var(--label);
  text-transform: uppercase;
  transition: color 0.2s ease-out;
  font-size: 13px;
  letter-spacing: 0.2px;
}
.input fieldset input:focus + small,
.input fieldset textarea:focus + small,
.input fieldset select:focus + small {
  color: var(--label-hover);
}
.input fieldset a {
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 100%;
  min-width: 35px;
  top: 0px;
  right: 0px;
  cursor: pointer;
  user-select: none;
  color: var(--input-alt);
}
.input fieldset a:before {
  content: "";
  position: absolute;
  width: 2px;
  height: 22px;
  top: 50%;
  left: -2px;
  border-radius: 5px;
  background: var(--input-alt);
  transform: translate(0, -50%);
}
.input fieldset a > strong {
  font-size: 11px;
}
.input fieldset a > strong > span {
  margin-left: 4px;
}
.input fieldset a:hover {
  color: var(--input);
}
.input fieldset a.right {
  left: 0px;
  right: auto;
  z-index: 2;
  width: 42px;
  background: var(--input-bg);
  border-radius: 4px 0 0 4px;
  border: 1px solid var(--input-border);
  border-right: none;
}
.input fieldset a.right:before {
  display: none;
}
.input fieldset p {
  display: block;
  position: absolute;
  top: 0px;
  right: 0px;
  right: 50px;
  line-height: 38px;
  white-space: nowrap;
  color: var(--input-alt);
  pointer-events: none;
}
.input fieldset a + input {
  padding-right: 40px;
}
.input h5 {
  display: block;
  width: 100%;
  border: 1px dashed var(--body-alt);
  font-size: 12px;
  line-height: 12px;
  margin: 32px 0 0 0;
  padding: 24px 0;
  font-size: 14px;
  text-align: center;
  cursor: pointer;
}
.input h5 svg {
  display: block;
  margin: 0 auto 20px;
  pointer-events: none;
}
.input h5 span {
  display: block;
  pointer-events: none;
  line-height: 18px;
}
.input > fieldset > strong {
  display: none;
  position: absolute;
  top: 58px;
  left: 8px;
  text-align: left;
  pointer-events: none;
  font-weight: 300;
  font-size: 12px;
  line-height: 12px;
  color: var(--body);
}
.input > fieldset > strong span {
  float: left;
  margin: -1px 4px 0 0;
}
.input.disabled {
  opacity: 0.25;
  pointer-events: none;
}
.input.padded {
  margin-bottom: 18px;
}
.input.seed fieldset .editable {
  padding: 0 32px 0 21px;
}
.input.capsLock input:focus + small + strong {
  display: block;
}
.input.capsLock > :before {
  content: "";
  display: block;
  position: absolute;
  top: 1px;
  right: 40px;
  width: 46px;
  height: 46px;
  background: var(--input-bg);
}

.info {
  display: block;
  position: absolute;
  top: 71px;
  right: -5px;
  font-size: 11px;
  color: var(--input);
  line-height: 14px;
  padding: 4px 8px 2px;
  border-radius: 0 0 4px 4px;
  background: var(--input-bg);
  margin: -1px 38px 0 0;
  border: 1px solid var(--input-border);
  border-top: none;
}

.checkbox {
  display: block;
  position: absolute;
  bottom: -10px;
  right: 0px;
  float: right;
  font-size: 10px;
  line-height: 14px;
  padding: 0 38px 2px 0;
  text-transform: uppercase;
  cursor: pointer;
}
.checkbox:after {
  content: "";
  width: 28px;
  height: 15px;
  position: absolute;
  top: 0px;
  right: 1px;
  border-radius: 15px;
  border: 1px solid var(--body);
}
.checkbox:before {
  content: "";
  width: 9px;
  height: 9px;
  position: absolute;
  top: 3px;
  right: 16px;
  border-radius: 9px;
  border: 1px solid var(--body);
  transition: all 0.2s cubic-bezier(0, 0, 0.2, 1);
}
.checkbox.on {
  color: var(--primary);
}
.checkbox.on:before {
  border-color: var(--primary);
  background: var(--primary);
  right: 4px;
}

.dropdown {
  position: absolute;
  top: 0;
  left: 0px;
  width: 100%;
  padding: 38px 0 0 0;
  z-index: 2;
  max-height: 40px;
  overflow: hidden;
}
.dropdown li {
  display: block;
  list-style: none;
  padding-left: 8px;
  width: 100%;
  text-align: left;
  line-height: 28px;
  background: var(--input-bg);
  color: var(--input-alt);
}
.dropdown li.selected {
  color: var(--input);
}
.dropdown li:last-of-type {
  padding-bottom: 8px;
  border-radius: 0 0 5px 5px;
}
.dropdown li:hover {
  color: var(--input);
}
.dropdown:hover {
  max-height: none;
}

.strike:after {
  content: "";
  position: absolute;
  width: 1px;
  height: 16px;
  border-radius: 2px;
  background: var(--input-alt);
  transform: rotate(45deg);
}
.strike:hover:after {
  background: var(--input);
}

.down {
  position: relative;
  z-index: 2;
  pointer-events: none;
}
.down:before {
  display: none;
}
.down:after {
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 6px 4px 0 4px;
  border-color: var(--input) transparent transparent transparent;
  position: absolute;
  right: 12px;
  top: calc(50% - 2px);
}

.qrScanner {
  display: block;
  margin: 0 auto;
  width: 320px;
  text-align: center;
}
.qrScanner video {
  transform: rotateY(180deg);
  max-width: 100%;
  margin-bottom: 50px;
  border: 2px solid var(--input);
}
.qrScanner > section {
  margin-bottom: 40px;
}

.score {
  display: flex;
  position: absolute;
  top: -17px;
  right: 0px;
}
.score span {
  display: block;
  height: 4px;
  width: 50px;
  background: var(--body-alt);
  margin-left: 2px;
}
.score[data-strength="1"] span:nth-child(1) {
  background: var(--negative);
}
.score[data-strength="3"] span:nth-child(1),
.score[data-strength="3"] span:nth-child(2), .score[data-strength="2"] span:nth-child(1),
.score[data-strength="2"] span:nth-child(2), .score[data-strength="1"] span:nth-child(1),
.score[data-strength="1"] span:nth-child(2) {
  background: var(--negative);
}
.score[data-strength="4"] span {
  background: var(--positive);
}

.hint {
  position: absolute;
  bottom: -28px;
  right: 0;
  font-size: 14px;
}

.valid {
  position: absolute;
  top: 11px;
  right: -40px;
  opacity: 0.25;
}
.valid.isValid {
  opacity: 1;
}
@media screen and (max-width: 860px) {
  .valid {
    right: 46px;
  }
}

.select {
  position: relative;
  z-index: 9;
}
.select ul {
  display: none;
}
.select.open ul {
  display: block;
  position: absolute;
  width: 100%;
  height: 300px;
  top: -32px;
  left: 0px;
  background: var(--box-bg);
  border-radius: 5px;
  padding: 10px 0px 10px 0;
  z-index: 2;
}
.select.open ul :global .ps {
  margin-right: 0px;
}
.select.open ul li {
  display: block;
  list-style: none;
  text-align: left;
  line-height: 36px;
  font-size: 14px;
  cursor: pointer;
  padding-left: 30px;
}
.select.open ul li:hover {
  background: var(--box-alt);
}
.select.open ul li.selected {
  font-weight: 600;
  position: relative;
}
.select.open ul li.selected:after {
  position: absolute;
  top: 12px;
  left: 13px;
  content: "";
  display: block;
  width: 5px;
  height: 9px;
  border: solid var(--body);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.number small {
  width: 100%;
}
.number fieldset > div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.number fieldset > div input {
  width: 68px;
  font-size: 28px;
  text-align: center;
  padding: 8px 0;
}
.number div > span {
  display: block;
  width: 26px;
  height: 26px;
  line-height: 24px;
  border-radius: 5px;
  cursor: pointer;
  color: var(--body);
  text-align: center;
  background: var(--box-bg);
  border: 1px solid var(--box-alt);
  margin: 0 18px;
}
.number div > span:hover {
  background: 1px solid var(--box-alt);
}
.number div > span span {
  cursor: pointer;
}

.tooltip {
  position: absolute;
  pointer-events: none;
  z-index: 2;
  font-size: 14px;
  background: var(--bar-bg);
  color: var(--bar);
  width: 342px;
  border-radius: 4px;
  font-family: "SourceCodePro";
  letter-spacing: 2px;
  word-wrap: break-word;
  line-height: 21px;
  padding: 12px 14px 12px 16px;
  top: 48px;
  left: 50%;
  transform: translate(-50%, 0);
  opacity: 0;
}
.tooltip mark {
  color: var(--primary);
}
.tooltip:after {
  content: "";
  position: absolute;
  top: -6px;
  left: calc(50% - 6px);
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 6px 6px 6px;
  border-color: transparent transparent var(--bar-bg) transparent;
}

input:focus + .tooltip {
  display: none;
}

.addressHidden {
  visibility: hidden;
  font-size: 14px;
  padding-left: 10px;
  padding-right: 40px;
}