.progress p {
  line-height: 18px;
  font-size: 13px;
  text-align: center;
  margin-bottom: 6px;
}
.progress > div {
  position: relative;
  display: inline-block;
  margin: 0 auto;
  width: 100%;
  height: 14px;
  border: 2px solid var(--primary);
  border-radius: 8px;
}
.progress > div > div {
  display: block;
  height: 100%;
  background: var(--primary);
  transition: width 2s cubic-bezier(0.55, 0, 0.1, 1);
}
.progress.large {
  margin-bottom: 38px;
}
.progress.large small {
  display: block;
  margin-top: 12px;
  font-size: 14px;
}